body {
  margin: 0;
  font-family: 'inter', system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	background-color: #e7f6ff;
}

* {
	font-family: 'inter', system-ui;
}

.wrapper {
	max-width: 1080px;
	margin: 50px auto;
	border-radius: 20px;
	background: #FFF;
	box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.04);
}

header {
	border-bottom: 1px solid #eaeaea;
	img {
		margin-right: 15px;
	}

	h3, p {
		margin: 0;
	}
}

.content {
	h1 {
		margin: 0;
	}
}

.sneakers {
	margin: 0 60px;
	padding-bottom: 20px;
	display: flex;
}



.button {
	background: #fff;
	border: 1px solid #f2f2f2;
	box-sizing: border-box;
	border-radius: 8px;
	height: 32px;
	width: 32px;
	cursor: pointer;
}

.search-block {
	position: relative;
	border: 1px solid #f3f3f3;
	border-radius: 10px;
	padding: 0 15px;

	.clear {
		position: absolute;
		right: 5%;
		top: 50%;
		transform: translateY(-50%);
		width: 18px;
		height: 18px;
	}

	.search {
		position: absolute;
		left: 13px;
		top: 15px;
		width: 16px;
		height: 16px;
		cursor: pointer;
	}

	input {
		border: 0;
		padding: 13px;
		font-size: 16px;
		width: 200px;
		margin-left: 10px;
	}
}

.all {
	overflow: auto;
	margin-bottom: 10px;
	z-index: 3;
	max-height: 720px;
}


.cartTotalBlock {

	li {
		display: flex;
		align-items: flex-end;
		margin-bottom: 20px;
	}

	div {
		flex: 1;
		height: 1px;
		border: 1px dashed #DFDFDF;
		position: relative;
		top: -4px;
		margin: 0 7px;
	}
}

.greenButton {
	position: relative;
	width: 100%;
	height: 55px;
	border-radius: 18px;
	background: #9DD458;
	border: none;
	color: #FFF;
	text-align: center;
	font-family: Inter;
	font-size: 16px;
	font-style: normal;
	font-weight: 600;
	line-height: normal;
	cursor: pointer;
	transition: all 0.2s;

	&:disabled {
		background-color: #bebebe !important;
		cursor: default !important;
		animation: button-loading 2s ease-in-out infinite;
	}

	&:hover {
		background: lighten(#9dd558, 5%);
		.arrowRight {
			transform: translateX(5px);
		}
	}

	&:active {
		background: darken(#9dd558, 5%);
	}

	.arrowRight {
		position: absolute;
		right: 30px;
		top: 20px;
		transition: all 0.2s;
	}
}

.cartItem {
	border: 1px solid #f3f3f3;
	border-radius: 20px;
	overflow: hidden;
	padding: 20px;

	.cartItemImg {
		height: 70px;
		width: 70px;
		background-size: contain;
		background-position: 0 -3px;
		background-repeat: no-repeat;
		margin-right: 20px;

	}

	p {
		font-size: 16px;
		margin: 0;
	}

	b {
		font-size: 14px;
	}

	.removeBtn {
		opacity: 0.5;
		cursor: pointer;
		transition: all 0.2s ease-in-out;

		&:hover {
			opacity: 1;
		}
	}
}

.cartEmpty {
  text-align: center;

  p {
    width: 280px;
    line-height: 24px;
  }

  .greenButton {
    width: 245px;
    margin-top: 20px;

    &:hover {
      img {
        transform: rotate(180deg) translateX(3px);
      }
    }

    img {
      position: relative;
      top: 1px;
      transform: rotate(180deg);
      margin-right: 15px;
      transition: transform 0.15s ease-in-out;
    }
  }
}

@keyframes button-loading {
	0% {
		opacity: 1;
	}
	50% {
		opacity: 0.7;
	}
	100% {
		opacity: 1;
	}
}